/* Hover gradient card effect with white gradient and subtle lift */
.hover-gradient-card {
  position: relative;
  overflow: hidden;
  transition: transform 220ms ease, box-shadow 220ms ease, background-color 220ms ease;
}

.hover-gradient-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
}

/* White gradient glow that fades in on hover */
.hover-gradient-card::after {
  content: "";
  position: absolute;
  inset: -1px; /* cover borders too */
  background: radial-gradient(80% 80% at 50% 0%, rgba(255, 255, 255, 0.18), rgba(255, 255, 255, 0) 70%),
              linear-gradient(180deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 240ms ease;
  pointer-events: none;
}

.hover-gradient-card:hover::after {
  opacity: 1;
}

/* Squares canvas base sizing */
