/* ===== Contact page readability overrides ===== */
.contact-page {
  position: relative;
}

/* Make cards solid and increase contrast */
.contact-page .hover-gradient-card {
  /* monochrome, subtle translucency */
  background-color: rgba(0, 0, 0, 0.65);
  backdrop-filter: blur(8px) saturate(100%);
  -webkit-backdrop-filter: blur(8px) saturate(100%);
  border: 1px solid rgba(255, 255, 255, 0.16);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.35);
}
.contact-page .hover-gradient-card::after {
  /* subtle sheen for depth */
  opacity: 0.28;
}
.contact-page .hover-gradient-card:hover::after {
  opacity: 0.45;
}

/* Text contrast */
.contact-page h1,
.contact-page h2,
.contact-page h3,
.contact-page p,
.contact-page label { color: rgba(255, 255, 255, 0.95); }

/* Inputs and textarea solid backgrounds */
.contact-page input,
.contact-page textarea {
  background-color: rgba(0, 0, 0, 0.55) !important;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.22) !important;
  color: #ffffff !important;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.04);
}
.contact-page input::placeholder,
.contact-page textarea::placeholder { color: rgba(255, 255, 255, 0.6); }

.contact-page input:focus,
.contact-page textarea:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.9) !important;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.35);
}

/* Submit button stronger fill */
.contact-page button[type="submit"] {
  background-color: #0f0f0f !important;
  color: #ffffff !important;
  border: 1px solid rgba(255, 255, 255, 0.5);
  transition: background-color 200ms ease, color 200ms ease, border-color 200ms ease, box-shadow 200ms ease;
}
.contact-page button[type="submit"]:hover {
  background-color: #ffffff !important;
  color: #000000 !important;
  border-color: #000000;
}
